.login{
    display: grid;
    place-items: center;
    height: 100vh;
}
.login >div>img{
    object-fit: contain;
    height: 150px; 
}
.login>div{
    display: flex;
    flex-direction: column;
}
.login>Button{
    width: 300px;
    background-color: #2e81f4;;
    color:#eff2f5;
    font-weight: 800;   
}
.login>Button:hover{
    background-color: #1852a3;
}